



*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#active{
  border-bottom: yellow 2px solid;
  color: yellow;
  transition: 0.5s;
}

#sidebarbg{
animation: sidebar 0.5s ease-out 0s 1 forwards;
}

@keyframes sidebar {
  from{
    height: 0px;
  }
  to{
    height: 100vh;
  }
}

@media (min-width: 200px) and (max-width:640px) {
  #homeMainDiv{
    height: 900px;
  }
}

